<template>
  <el-container class="home-container">
    <!-- 头部区域 -->
    <el-header>
      <div class="logo">
        <img src="../assets/heima.png" alt="">
        <span>后台管理</span>
      </div>
      <a href="javascript:;" @click="logout">退出登录</a>
    </el-header>

    <!-- 内容区域 -->
    <el-container>
      <!-- 侧边栏 -->
      <el-aside :width="drawerAside?'64px':'200px'">
        <div class="drawer" @click="drawer"></div>
        <el-menu background-color="#333744" :collapse="drawerAside" :collapse-transition="false" unique-opened router :default-active="activeMenu" text-color="#fff" active-text-color="#409eff">
          <el-submenu index="welcome">
            <!-- 欢迎--一级菜单模板区域 -->
            <template slot="title">
              <i class="el-icon-magic-stick"></i>
              <span>欢迎</span>
            </template>
            <!-- 欢迎--二级菜单 index表示选中的操作，需要分配不同的index，否则会牵一发而动全身-->
            <el-menu-item index="/welcome" @click="saveNavActive('/welcome')">
              <i class="el-icon-menu"></i>
              <span slot="title">欢迎界面</span>
            </el-menu-item>
          </el-submenu>
          <!-- 一级菜单 index要是string类型 -->
          <el-submenu :index="item.id +''" v-for="item in asideList" :key="item.id">
            <!-- 一级菜单模板区域 -->
            <template slot="title">
              <i :class="iconFont[item.id]"></i>
              <span>{{item.authName}}</span>
            </template>
            <!-- 二级菜单 index表示选中的操作，需要分配不同的index，否则会牵一发而动全身-->
            <el-menu-item :index="'/'+sec_item.path" @click="saveNavActive('/'+sec_item.path)" v-for="sec_item in item.children" :key="sec_item.id">
              <i class="el-icon-menu"></i>
              <span slot="title">{{sec_item.authName}}</span>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>

      <!-- 主体内容部分 -->
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  name: 'Home',
  data() {
    return {
      asideList: [],
      // aside侧边栏的一级菜单标签
      iconFont: {
        125: 'iconfont icon-users',
        103: 'iconfont icon-tijikongjian',
        101: 'iconfont icon-shangpin',
        102: 'iconfont icon-danju',
        145: 'iconfont icon-baobiao',
      },
      // 显示和隐藏侧边栏
      drawerAside: false,
      // 当前激活菜单
      activeMenu: '',
    }
  },
  methods: {
    // 退出登录
    logout() {
      // 删除token
      window.sessionStorage.removeItem('token')
      window.sessionStorage.removeItem('activeMenu')
      // 跳转页面
      this.$router.push('/login')
    },

    // 请求侧边栏数据数据
    async initAsideList() {
      let { data: res } = await this.$http.get('menus')
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.asideList = res.data
    },

    // 显示隐藏侧边栏
    drawer() {
      this.drawerAside = !this.drawerAside
    },

    // 将点中的二级菜单的index存储到sessionStorage中
    saveNavActive(activeMenu) {
      window.sessionStorage.setItem('activeMenu', activeMenu)
      this.activeMenu = activeMenu
    },
  },
  created() {
    this.initAsideList()
    if (!window.sessionStorage.getItem('activeMenu')) {
      window.sessionStorage.setItem('activeMenu', '/welcome')
    }
    this.activeMenu = window.sessionStorage.getItem('activeMenu')
  },
}
</script>

<style lang="less" scoped>
.home-container {
  height: 100%;
}

// 顶部
.el-header {
  display: flex;
  height: 78px !important;
  justify-content: space-between;
  align-items: center;
  background-color: #373d41;
  .logo {
    display: flex;
    justify-content: start;
    align-items: center;
    span {
      padding: 10px;
      font-size: 25px;
      color: #fff;
    }
  }
  a {
    font-size: 18px;
    color: #fff;
  }
}

// 侧边栏
.el-aside {
  position: relative;
  overflow: visible;
  background-color: #333744;
}
// 侧边栏图片与文字的距离
.iconfont {
  margin-right: 5px;
}
// 侧边栏突起的边框
.el-menu {
  border: 0;
}
// 抽屉
.drawer {
  position: absolute;
  top: 230px;
  right: -50px;
  width: 0;
  height: 0;
  border-left: 20px solid #333744;
  border-right: 30px solid transparent;
  border-top: 30px solid transparent;
  border-bottom: 30px solid transparent;
  cursor: pointer;
}

// 主体内容
.el-main {
  background-color: #eaedf1;
}
</style>